<template>
  <div>
    <h2>home</h2>
    <router-link v-if="!this.$store.getters.getIsAuth" to="/login"></router-link>
    <button v-else v-on:click="this.onLogout" ></button>
    <router-link to="/signup"></router-link>
    <router-link to="/profile"> </router-link>
    <router-link to="/test"></router-link>
    <p>status : {{ this.$store.getters.getIsAuth  }}</p>
  </div>
  
</template>
<script>
import { mapGetters } from 'vuex'
import { mapMutations, mapActions } from 'vuex';

export default {
  ...mapActions(['getAuthBool']),
  
  methods : {
    
    onLogout(){
      console.log('onLogout')
        this.$store.dispatch('getAuthBool',{ isAuth : !this.$store.getters.getIsAuth })
          return  this.$store.getters.getIsAuth && this.$router.push({name : 'Home'})//     
    }
  }
}
</script>

