import {DOMAIN} from './../../constants';
import {createRequest, successHandler, errerHandler} from './../index';

/**
 *     
 * @param {*} email ex lukas.sin
 * @param {*} pwd ex 1234
 */
export const login = (email,pwd, next)=>{
  createRequest({
    method:'post',
    url:`${DOMAIN}/api/v1/user/login`,
    data:{email,pwd}
  })
  .then(successHandler)
  .then(next)
  .catch(errerHandler)
}

export const create = (data,next)=>{
  console.log(data)
  /* */
  createRequest({
    method:'post',
    url:`${DOMAIN}/api/v1/user/create`,
    data : {
      ...data  
    }
  })
  .then(successHandler)
  .then(next)
  .catch(errerHandler)
  

}
export const update = (data,next)=>{
  console.log(data)
  const infoData = Array.from(Object.keys(data))
  const validKeys = infoData.filter((item)=> data[item])
  let updateData = {};
  if(validKeys.length){
    validKeys.forEach( item => {
      updateData[item] = data[item]
    });
  }
  createRequest({
    method:'post',
    url:`${DOMAIN}/api/v1/user/update`,
    data : {
      email : data['email'],
      data:{
        ...updateData
      },
    }

  })
  .then(successHandler)
  .then(next)
  .catch(errerHandler)
}
export const remove = (data)=>{
  createRequest({
    method:'get',
    url:`${DOMAIN}/api/v1/user/delete` + data,
  })
  .then(successHandler)
  .then(next)
  .catch(errerHandler)
}
export const getList = ()=>{
  createRequest({
    method:'get',
    url:`${DOMAIN}/api/v1/user/list/`,
  })
  .then(successHandler)
  .then(next)
  .catch(errerHandler)
}
export const getUserByEmail = (email,next)=>{
  createRequest({
    method:'get',
    url:`${DOMAIN}/api/v1/user/list/`+email,
  })
  .then(successHandler)
  .then(next)
  .catch(errerHandler)
}

